* {
    font-size: 1rem;
    font-weight: 300;
    box-sizing: border-box;
}

li {
    list-style: none;
    margin: .5rem 0;
}

.container {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 2.5rem 9rem 0 9rem;
    font-size: 1.1rem;
    line-height: 1.7rem;
    padding: 1rem;
    background-color: rgb(54, 57, 63);
}

.img {
    margin-right: 2rem;
    width: 250px;
}

.infoContainer {
    max-width: 600px;
    width: 100%;
    /* flex: 1 1 0%; */
    padding: 0px 30px;
}

.title {
    font-size: 2rem;
    font-weight: 300;
}

.overview {
    text-align: justify;
    padding-bottom: 1rem;
    line-height: 24px;
}

.genres {
    padding-right: 1rem;
    color: rgb(33, 150, 243);
}

.genres:hover {
    cursor: pointer;
    color: rgb(25, 116, 190);
}

.companies {
    padding-right: 1rem;
}

@media (max-width: 600px) {
    .container {
        flex-direction: column;
        align-items: center;
        margin: 2rem 0;
    }

    .infoContainer {
        text-align: center;
        display: flex;
        flex-direction: column;
    }

    .title {
        margin-top: 2rem;
        line-height: 2.5rem;
    }

    .img {
        margin: 0;
    }

}