.container {
    display: flex;
    align-items: center;

}

.svg {
    width: 1.8rem;
    display: inline-block;
    vertical-align: middle;
    position: relative;
    left: 3rem;
    margin-right: .5rem;
}

.input {
    padding: .8rem 3rem;
    font-size: 1.2rem;
    background-color: transparent;
    border: none;
    outline: none;
    color: rgb(220, 221, 222);
    caret-color: rgb(220, 221, 222);
    margin-left: .5rem;
}


@media (max-width: 600px) {
    .container {
        width: 100%;
        display: flex;
        flex-direction: row;
        -webkit-box-pack: start;
        justify-content: flex-start;
        -webkit-box-align: center;
        align-items: center;
        flex-wrap: wrap;
    }

    .svg {
        width: 1.5rem;

    }

    .input {
        padding: .8rem 2.5rem;
        font-size: 1rem;
    }
}